export default function Camarotes() {
  //const styles = useStyles();
  let produtos = useSelector(state => state.products.grouped_products);
  produtos = produtos.filter(produto => {
    return produto.categories[0].id === 16;
  });
  return (
    <div>
      <Trilha pagename="Camarotes" />

      <h1 className="titulo-internas">Camarotes</h1>

      <div className="entry-content">
        <p>
          Os Camarotes do Carnaval de Salvador são estruturas fixas ao longo do
          Circuito Barra-Ondina. Nos Camarotes acontecem shows exclusivos e uma
          grande festa a parte, possuem ambientes diversos e um amplo serviço.
          Os Camarotes podem ser All Inclusive, oferecendo bebida e alimentação
          incluídas ou Open Bar com apenas bebidas disponíveis. Os Camarotes
          também possuem mirante com vista privilegiada para onde ocorrem os
          desfiles de trios e blocos do Carnaval de Salvador.
        </p>
        <p>
          <b>
            Confira abaixo os principais Camarotes e clique para informações dos
            Camarotes do Carnaval de Salvador 2020.
          </b>
        </p>
        <Grid container>
          {produtos.map(item => (
            <Grid item xs={12} key={item.id}>
              <Link key={item.id} to="/compre-aqui" component={RouterLink}>
                <Produto dados={item} tamanho={"100%"} link={"compre-aqui"} />
              </Link>
            </Grid>
          ))}
        </Grid>
      </div>
    </div>
  );
}
